
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  width: 100vw;
  margin: 0;
}
body {
  margin: 0;
}
nav {
  position: absolute;
  top: 0px;
  left: 0px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  background-color: white;
  z-index: 99;
  width: 100px;
  padding: 20px;
}
@media (max-width: 768px) {
nav {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-pack: start;
          -ms-flex-pack: start;
              justify-content: flex-start;
      height: 50px;
      width: 100%;
}
}
.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 400px;
}
@media (max-width: 768px) {
.wrapper {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      width: 100vw;
      height: 50px;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      margin-top: 10px;
}
}
.links {
  color: #9e9c9c;
  text-decoration: none;
  font-size: 1.3em;
  -webkit-transition: all 400ms;
  transition: all 400ms;
  font-weight: normal;
}
.links:hover {
    color: #414141;
    margin-left: 12px;
}
@media (max-width: 768px) {
.links {
      font-size: 1.1em;
      margin: 2%;
}
.links:hover {
        margin-top: 3%;
        margin-left: 0%;
}
}
router-link {
  color: black;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.fade2-enter-active, .fade2-leave-active {
  -webkit-transition: opacity 0.6s;
  transition: opacity 0.6s;
}
.fade2-enter, .fade2-leave-active {
  opacity: 0;
}
.menuBtn {
  position: absolute;
  top: 10px;
  left: 20px;
  border: none;
  background: none;
  font-size: 2em;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0;
  margin: 0;
  -webkit-filter: opacity(0.3);
          filter: opacity(0.3);
}
.menuBtn:focus {
    outline: 0;
}
.menuBtn:hover {
    -webkit-filter: opacity(0.7);
            filter: opacity(0.7);
}
@media (max-width: 768px) {
.menuBtn {
      top: 30px;
}
}



